.nav,
.nav__menu {  
  &__link,
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;

    font-family: 'Mont-Bold', sans-serif;
    font-weight: 800;
    font-size: 12px;
    color: $color-gray;

    text-align: center;
    
    letter-spacing: 0.04em;
    text-transform: uppercase;

    @include onDesktop {
    height: $big-size;
    }
    
      &:hover {
        color: $color-white;
      }

      &__active {
        position: relative;
    
        &::after {
          content: '';
    
          display: block;
          height: 3px;
    
          position: absolute;
          bottom: 6px;
          left: 0;
          right: 0;
    
          background-color: $color-white;
        }
    
        color: $color-white;
      }
    }
}

.nav {
  display: none;
  height: $medium-size;

  @include onTablet {
    display: flex;
    margin-right: auto;
  }

  &__list {
    display: flex;
  }

  &__link {
    margin-right: $big-size;
  }
}

.nav__menu {
  display: flex;
  justify-content: center;

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__link {
    height: 32px;
    width: min-content;
  }
}